@import '../../styles/includes';

.page.home {
  position: relative;
  width: 100%;
  // height: 100%;
  padding: vw(50) 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.corn-metamask-container {
  position: relative;
  margin: 15px 0 0 0;
}

.corn-button-row {
  position: relative;
  margin: 15px 0;

  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;

  & > * {
    margin: auto 15px;
  }
}

.corn-ticket-row,
.corn-ticket-input {
  // font-size: calc(min(30px, #{vw(60, 1920, 18px)}));
  &,
  span {
    font-size: 30px;
  }
}

span.corn-ticket-number {
  font-size: vw(45, 1920, 22px);
}

.corn-button-row {
  .corn-ticket-input {
    width: vw(180, 1920, 90px);
  }
  .corn-ticket-button {
    width: vw(150, 1920, 90px);
  }
  .corn-add-button {
    margin: 0 0 15px 0;
    &:not(:last-of-type) {
      margin-right: 15px;
    }
  }
  .corn-remove-button {
    margin: 0 0 15px 0;
    width: vw(150, 1920, 130px);
  }
}

.corn-ticket-button,
.corn-add-button,
.corn-remove-button,
.ticket-input-button:not(.is-top) {
  .corn-button-text {
    margin-bottom: 1.5%;
  }
}


.corn-ticket-row {
  position: relative;
  width: 600px;
  max-width: 95vw;

  margin: vw(10, 1920, 5px);

  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;

  font-weight: 900;
  font-stretch: normal;
  font-style: normal;

  & > * {
    &:last-child {
      margin-right: 30px;
    }
  }
}

.corn-ticket-number {
  position: relative;
  display: inline-block;
  text-align: right;
  min-width: 60px;
  margin-right: vw(20, 1920, 5px);
  color: $textBorderColor;
}

.corn-ticket-input {
  position: relative;
  display: inline-block;
  width: vw(300, 1920, 50px);
  max-width: 120px;
  text-align: center;
  // flex: 1;
  margin: auto vw(15);
  padding-left: 0;
  padding-right: 0;

  border-radius: 999px;
  border: none;
  background-color: $darkBgColor;
  color: $lightGreenColor;
}

.ticket-input-container {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  .corn-ticket-input {
    border-radius: 0;
  }
  .ticket-input-button {
    width: vw(300, 1920, 50px);
    max-width: 120px;
    height: vw(38, 1920, 22px);
    .corn-button-text {
      font-size: vw(30, 1920, 15px);
    }
    &.is-top {
      border-radius: 999px 999px 0 0;
    }
    &.is-bottom {
      border-radius: 0 0 999px 999px;
    }
  }
}


.corn-ticket-dice,
.corn-ticket-delete {
  position: relative;
  object-fit: contain;
  width: vw(50, 1920, 25px);
  height: vw(50, 1920, 25px);
  min-width: vw(50, 1920, 25px);
  min-height: vw(50, 1920, 25px);
  margin: auto vw(15);
  user-select: none;
  cursor: pointer;
}

.corn-send-button {
  margin: 15px 0 0 0;
}

.corn-donate-container {
  position: relative;
  margin: 15px 10vw 0 10vw;
  text-align: center;
}

.corn-donate-text {
  position: relative;
  margin: 10px 20px;
  white-space: pre-line;
}

.corn-icon-donate {
  position: relative;
  object-fit: contain;
  width: 32px;
  height: 32px;
}